@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c&display=swap);
/*@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@500&display=swap');*/

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{
  font-family: 'Noto Serif JP Vertical';
  src: url(../../static/media/NotoSerifJP-Medium-Vertical.8c6b1df4.woff2) format('woff2'),
        url(../../static/media/NotoSerifJP-Medium-Vertical.d578d949.woff) format('woff'),
        url(../../static/media/NotoSerifJP-Medium-Vertical.fc084e09.otf) format('otf');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
/* bold */
@font-face {
  font-family: 'Noto Serif JP Vertical';
  src: url(../../static/media/NotoSerifJP-Bold-Vertical.95a71858.woff2) format('woff2'),
        url(../../static/media/NotoSerifJP-Bold-Vertical.b2b80596.woff) format('woff'),
        url(../../static/media/NotoSerifJP-Bold-Vertical.8cb46ee7.otf) format('otf');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
img {
    max-width:100%;
    height:auto;
    animation:fadeIn 0.5s ease 0s 1 normal;
	-webkit-animation:fadeIn 0.5s ease 0s 1 normal;
}

@-webkit-keyframes fadeIn {
	0%{opacity:0;}
	100%{opacity:1;}
}

@keyframes fadeIn {
	0%{opacity:0;}
	100%{opacity:1;}
}
    
